import React, { useRef, useState } from "react";
import CryptoAES from "crypto-js/aes";
import CryptoUTF8Enc from "crypto-js/enc-utf8";

import "./styles.scss";

export default function Crypto() {
  const keyInputEl = useRef<HTMLInputElement>(null);
  const encryptInputEl = useRef<HTMLTextAreaElement>(null);
  const decryptInputEl = useRef<HTMLTextAreaElement>(null);
  const [cipherText, setCipherText] = useState("");
  const [originalText, setOriginalText] = useState("");

  const encrypt = () => {
    let encryptionResult: string;
    try {
      encryptionResult = CryptoAES.encrypt(
        encryptInputEl?.current?.value ?? "",
        keyInputEl?.current?.value ?? ""
      ).toString();
    } catch (error) {
      encryptionResult =
        (error as Error)?.message ?? "ERROR::Failed to encrypt";
    }

    decryptInputEl!.current!.value = encryptionResult;
    setCipherText(encryptionResult);
  };

  const decrypt = () => {
    let decryptionResult: string;
    try {
      const bytes = CryptoAES.decrypt(
        decryptInputEl?.current?.value ?? "",
        keyInputEl?.current?.value ?? ""
      );
      decryptionResult = bytes.toString(CryptoUTF8Enc);
    } catch (error) {
      decryptionResult =
        (error as Error)?.message ?? "ERROR::Failed to decrypt";
    }

    encryptInputEl!.current!.value = decryptionResult;
    setOriginalText(decryptionResult);
  };

  return (
    <div className="Crypto">
      <h1 className="font-bold text-xl py-4">AES (CBC w/o PBKDF2)</h1>
      <input
        autoFocus
        placeholder="Secret Key ðŸ—"
        className="key"
        type="text"
        ref={keyInputEl}
      />
      <form className="form">
        <textarea
          ref={encryptInputEl}
          className="lhsInput"
          placeholder="Plain text"
        />
        <div className="flex flex-col">
          <button
            onClick={encrypt}
            type="button"
            className="btn rounded-r-full"
          >
            Encrypt â‡¨
          </button>
          <button
            onClick={decrypt}
            type="button"
            className="btn rounded-l-full"
          >
            â‡¦ Decrypt
          </button>
        </div>
        <textarea
          ref={decryptInputEl}
          className="rhsInput"
          placeholder="Encrypted text"
        />
      </form>
    </div>
  );
}
